<template>
	<transition name="fade" appear>
		<div class="container-fluid storeList">
			<div id="leStore" class="row">
				<h1>Le store</h1>
			</div>
			<div class="row">
				<div class="vinyles">
					<div class="container">
						<div class="row">
							<div class="col-sm-8 col-xs-12">
								<div class="row">
									<div class="col-xs-12">
										<h2>Vinyles</h2>
									</div>
								</div>
								<div class="row">
									<div class="col-xs-12">
										<p>Fouillez, découvrez, cherchez, trouvez ! Les dernières nouveautés comme les grands classiques sont au Vinyl Store !</p>
										<h3>Nos coups de coeur : </h3>
									</div>
								</div>
								<div class="row">
									<div class="productList">
										<div class="col-sm-3 col-xs-6" v-for="product in coupDeCoeur('vinyles','coupdecoeur', 5)">
											<div class="vinyleItem">
												<router-link :to="{ name: 'product', params: { type:'vinyles' , id: product.id }}"><img :src="/vinyle/+product.image" /></router-link>
											</div>
										</div>
									</div>
								</div>
							</div>
							<div class="col-xs-4 hidden-xs">
								<div class="productContent">
									<div class="row">
										<div class="col-xs-12">
											<div v-for="product in coupDeCoeur('vinyles','coupdecoeur', 1)" class="vinyleItem">
												<router-link :to="{ name: 'product', params: { type:'vinyles' , id: product.id }}"><img  :src="/vinyle/+product.image" /></router-link>
											</div>
										</div>
									</div>
									<div class="row">
										<div class="col-md-12">
											<h4>Sélection nouveautés</h4>
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
			<div class="row">
				<div class="platines">
					<div class="container">
						<div class="row">
							<div class="col-md-8">
								<div class="row">
									<div class="col-xs-12">
										<h2>Platines</h2>
									</div>
								</div>
								<div class="row">
									<div class="col-xs-12">
										<p>Pour écouter les meilleurs disques, il faut le meilleur matériel.</p>
										<h3>Nos dernières platines : </h3>
									</div>
								</div>
								<div class="row">
									<div class="productList">
										<div class="col-xs-6 col-md-3" v-for="product in coupDeCoeurPlatines('platines', 'coupdecoeur' , 5)">
											<div class="vinyleItem">
												<router-link :to="{ name: 'product', params: { type:'platines' , id: product.id }}"><img :src="/vinyle/+product.image" /></router-link>
											</div>
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
			<div class="row">
				<div class="coffrets">
					<div class="container">
						<div class="row">
							<div class="col-sm-8 col-xs-12">
								<div class="row">
									<div class="col-xs-12">
										<h2>Coffrets</h2>
									</div>
								</div>
								<div class="row">
									<div class="col-xs-12">
										<p>Retrouvez les coffrets des mois précédents.</p>
									</div>
								</div>
								<div class="row">
									<div class="productList">
										<div class="col-sm-3 col-xs-6" v-for="product in coupDeCoeur('coffrets','month', 5)">
											<div class="vinyleItem">
												<router-link :to="{ name: 'product', params: { type:'coffrets' , id: product.id }}"><img :src="/vinyle/+product.image" /></router-link>
											</div>
										</div>
									</div>
								</div>
							</div>
							<div class="col-xs-4 hidden-xs">
								<div class="productContent">
									<div class="row">
										<div class="col-xs-12">
											<div v-for="product in coupDeCoeur('coffrets','month', 1)" class="vinyleItem">
												<router-link :to="{ name: 'product', params: { type:'coffrets' , id: product.id }}">
													<img :src="/vinyle/+product.image" />
												</router-link>
											</div>
										</div>
									</div>
									<div class="row">
										<div class="col-xs-12">
											<h4>Le coffret du mois</h4>
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</transition>
</template>

<script>
export default {
	data () {
		return {
			products: [],
			platines: []
		}
	},
	mounted () {
		this.$http.get('http://corentindechomet.fr/vinyle/api/products').then((response) => {
			console.log("success", response)
			this.products = response.data
		}, (response) => {
			console.log("erreur", response)
		}),
		this.$http.get('http://corentindechomet.fr/vinyle/api/platines').then((response) => {
			console.log("success", response)
			this.platines = response.data
		}, (response) => {
			console.log("erreur", response)
		})
	},
	methods: {
		coupDeCoeur(type, genre, limit) {
			var resultatsArray = this.products
			var filtre = [type, genre]

			resultatsArray = resultatsArray.filter(function(item){
				if(item.type.toLowerCase().indexOf(filtre[0]) !== -1 && item.theme.toLowerCase().indexOf(filtre[1]) !== -1){
					return item;
				}
			})


			resultatsArray.sort(function(a,b){
				var myA = a.date;
				var myB = b.date;
				if(myA < myB) {
					return 1;
				}
				if(myA > myB) {
					return -1;
				}
				return 0;
			})
			if(limit == 1) {
				return resultatsArray.slice(0,1)
			}
			else{
				return resultatsArray.slice(1,limit)
			}
		},
		coupDeCoeurPlatines(type, genre, limit) {
			var resultatsArray = this.platines
			var filtre = [type, genre]

			resultatsArray = resultatsArray.filter(function(item){
				if(item.type.toLowerCase().indexOf(filtre[0]) !== -1){
					return item;
				}
			})


			resultatsArray.sort(function(a,b){
				var myA = a.date;
				var myB = b.date;
				if(myA < myB) {
					return 1;
				}
				if(myA > myB) {
					return -1;
				}
				return 0;
			})
			if(limit == 1) {
				return resultatsArray.slice(0,1)
			}
			else{
				return resultatsArray.slice(1,limit)
			}
		}
	}
}
</script>



